import React, { useState } from 'react';
import Page from 'components/Page';
import WithFooter from 'components/WithFooter';
import Input from 'components/Input';
import Button from 'components/Button';

const EditFieldForm = ({ history, name, pageTitle, label, initialValue }) => {
  const valueToInit =
    localStorage.getItem(`form-value-${name}`) || initialValue;
  const [value, setValue] = useState(valueToInit);

  const submit = () => {
    localStorage.setItem(`form-value-${name}`, value);
    history.goBack();
  };

  return (
    <Page
      className="lowLevelPage"
      title={pageTitle}
      onBackNavigation={() => history.goBack()}
    >
      <WithFooter
        footer={
          <div style={{ textAlign: 'center', padding: '27px 0' }}>
            <Button onClick={submit}>Update</Button>
          </div>
        }
      >
        <div>
          {/* This div is necessary because it'll be flex-item, <Input> returns <label> which shouldn't be flex-item */}
          <Input name={name} label={label} value={value} onChange={setValue} />
        </div>
      </WithFooter>
    </Page>
  );
};

export default EditFieldForm;
